body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  padding: 0;
}

.navbarNav {
  color: black;
}

ul {
  list-style-type: none;
}
/*the header css code below is used to style a border on the bottom to have a dashed black line*/

h4 {
  color: black;
}
a {
  color: black;
}

/*the nav-link css code below is used to style the list of navigational points that sit in the right side of the navbar.*/
.nav-link {
  list-style-type: horizontal;
  margin-right: 0;
  float: left;
  color: black;
  margin-bottom: 0;
}

.navbar-brand {
  margin-left: 0;
  padding: 20px;
  margin-bottom: 0;
  color: black;
  width: 20px;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 50px;
}

